import React, {Component} from 'react';
import {Modal, Button, Icon, Header} from 'semantic-ui-react';

const Astro = ({astro, modalOpen, handleOpen, handleClose}) => {
  return (
      <Modal
          trigger={<Button onClick={handleOpen}>Show Modal</Button>}
          open={modalOpen}
          onClose={handleClose}
          basic
          size='small'
      >
        <Header icon='browser' content='Purchase this Star'/>
        <Modal.Content>
          <p>{`Do you like star #${astro.target.name}? ${'\n'} Buy now and put your identity on constellation using MetaID(Blockchain based self-soverign identity service)`}</p>
        </Modal.Content>
        <Modal.Actions>
          <div style={"display: flex; justify-content: 'flex-between';"}>
            <a href='https://metadium.com' target="_blank">
              <Icon name='remove' /> What is MetaID?
            </a>
            <Button color='green' onClick={handleClose} inverted>
              <Icon name='checkmark'/> Open Metamask
            </Button>
          </div>
        </Modal.Actions>
      </Modal>
  );
};

export {Astro}


